@media (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }
    .title {
        font-size: 16px;
        margin-top: 15px;
        line-height: 16px; }
    .subtitle {
        font-size: 14px;
        line-height: 21px;
        margin-top: 11px; }
    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 16px; }

        &_phone {
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 11px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 9px;
            margin-top: 6px; } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px;
            font-size: 16px; } }
    .menu {
        width: 85%;
        position: fixed;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        &_link {
            display: block; } }
    .reasons {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px; }
        &_round {
            width: 100px;
            height: 100px; }
        &_descr {
            width: 100%;
            margin-top: 27px; }
        &_subtitle {
            font-size: 16px;
            text-align: left; } }
    .hamburger {
        &_active {
            span {
                &:nth-child(1) {
                    transform: translateY(5px) rotate(-45deg); }
                &:nth-child(3) {
                    transform: translateY(5px) rotate(45deg); } } } } }
